.information {
    position: fixed;
    top: 180px;
    right: 20px;
    max-width: 40%;
    padding: 4px;
    box-shadow: 0px 1px 5px rgba(0, 0, 0, 0.2), 0px 3px 4px rgba(0, 0, 0, 0.12), 0px 2px 4px rgba(0, 0, 0, 0.14);
    border-radius: 8px;
    text-align: center;

    @media screen and (min-width: 480px) {
        max-width: 300px;
        top: 100px;
        right: 0;
        left: 55%;
        padding: 12px;
    }
}

.information__image {
    max-height: 160px;
    object-fit: contain;
}

.information__name {
    margin-top: 8px;
    font-family: 'Pokemon Hollow';
    font-size: 16px;
    text-transform: capitalize;

    @media screen and (min-width: 480px) {
        font-size: 26px;
    }
}

.information__table {
    width: 100%;
    margin-top: 8px;
    border: 1px solid #000;
    border-collapse: collapse;
    font-size: 9px;

    @media screen and (min-width: 480px) {
        font-size: 12px;
    }
}

.information__cell{
    padding: 4px 12px;
    border: 1px solid #000;
}